export default async function loadModelVersion(args) {
    const defaults = {
        version: 4,
        ...args
    }
    const maxVersion = 4
    const models = []
    try {
        for (let i = 0; i <= maxVersion; i++) {
            const module = await import(`./model.v${i}.js`)
            models.push(module)
        }
        return new models[Number(defaults.version)].default(args)
    } catch (error) {
        console.error(
            `Failed to load model version ${defaults.version}:`,
            error
        )
        throw error
    }
}
